import React from 'react';
import { css } from '@emotion/core';
import { Link } from 'gatsby';
import { colors, viewports } from '../../utils/variables';

const Button = ({ children, url, theme, style }) => {
  const isSecondary = theme === 'secondary';
  return (
    <Link
      to={url}
      css={css`
        ${baseCSS}
        background-color: ${isSecondary ? 'transparent' : colors.blue};
        border: 2px ${isSecondary ? '#fff' : colors.blue} solid;

        @media ${viewports.medium} {
          &:hover {
            background-color: ${isSecondary ? '#fff' : colors.darkBlue};
            border-color: ${isSecondary ? '#fff' : colors.darkBlue};
            color: ${isSecondary ? colors.darkBlue : '#fff'};
          }
        }

        ${style}
      `}
    >
      {children}
    </Link>
  );
};

export default Button;

const baseCSS = css`
  align-items: center;
  border-radius: 1.25rem;
  color: #fff;
  display: flex;
  font-family: 'Dosis';
  font-weight: 700;
  height: 2.5rem;
  justify-content: center;
  letter-spacing: 0.02em;
  margin: 0 auto;
  text-decoration: none;
  width: 8.75rem;
`;
