import React from 'react';
import { css } from '@emotion/core';
import { styles, colors, viewports } from '../../utils/variables';
import { CopyHeading } from '../styled/Typography';

const Features = ({ features }) => {
  return (
    <div css={featuresCSS}>
      {features.map(({ feature }) => {
        return (
          <div css={featureCSS}>
            <div css={iconCSS} />
            <CopyHeading style={copyHeadingCSS} key={feature}>
              {feature}
            </CopyHeading>
          </div>
        );
      })}
    </div>
  );
};

export default Features;

const featuresCSS = css`
  background-color: #fff;
  box-shadow: ${styles.boxShadow};
  margin-bottom: 1.5rem;
  padding: 2.1875rem 2rem;

  @media ${viewports.medium} {
    margin-bottom: 0;
    width: 48%;
  }
`;

const featureCSS = css`
  align-items: center;
  display: flex;
  margin-bottom: 1.5rem;

  &:last-of-type {
    margin-bottom: 0;
  }
`;

const iconCSS = css`
  background-color: ${colors.gray};
  border-radius: 50%;
  height: 2.5rem;
  margin-right: 1.5rem;
  min-width: 2.5rem;
`;

const copyHeadingCSS = css`
  font-size: 1.25rem;
`;
