import React from 'react';
import { css } from '@emotion/core';
import { CopyHeading, BodyText } from '../styled/Typography';
import { colors, viewports } from '../../utils/variables';
import Button from '../styled/Button';

const ProductInfo = ({ product, theme }) => {
  const { name, description, cta, cta_url } = product;
  const isSecondary = theme === 'secondary';
  return (
    <div css={baseCSS}>
      <CopyHeading
        style={css`
          color: ${isSecondary ? '#fff' : colors.black};
        `}
        theme="large"
      >
        {name}
      </CopyHeading>
      <BodyText
        style={css`
          border-left: 2px ${isSecondary ? '#fff' : colors.gray} solid;
          color: ${isSecondary ? '#fff' : colors.gray};
          padding-left: 1.5rem;

          & > p {
            margin-bottom: 0;
          }
        `}
      >
        {description}
      </BodyText>
      <Button url={cta_url} style={buttonCSS} theme={theme}>
        {cta}
      </Button>
    </div>
  );
};

export default ProductInfo;

const baseCSS = css`
  display: flex;
  flex-direction: column;

  @media ${viewports.medium} {
    width: 48%;
  }
`;

const buttonCSS = css`
  margin: 1.5rem 0 0;

  @media ${viewports.medium} {
    margin: auto 0 0;
  }
`;
