import React from 'react';
import { css } from '@emotion/core';
import Container from '../styled/Container';
import Features from './Features';
import ProductInfo from './ProductInfo';
import { viewports } from '../../utils/variables';

const ProductExcerpts = ({ products }) => {
  return products.map((product, index) => (
    <article css={articleCSS} key={product.name}>
      <Container theme="secondary" style={containerCSS}>
        <Features features={product.features} />
        <ProductInfo product={product} theme={index % 2 === 0 && 'secondary'} />
      </Container>
    </article>
  ));
};

const articleCSS = css`
  padding: 4.375rem 0;

  &:nth-of-type(2n - 1) {
    background-color: #717171;

    & > div {
      flex-direction: row;
    }
  }
`;

const containerCSS = css`
  flex-direction: row-reverse;
  justify-content: space-between;

  @media ${viewports.medium} {
    display: flex;
  }
`;

export default ProductExcerpts;
