import React from 'react';
import { graphql } from 'gatsby';
import { css } from '@emotion/core';
import Layout from '../components/shared/Layout';
import SecondaryPageWrapper from '../components/styled/SecondaryPageWrapper';
import TopBanner from '../components/shared/TopBanner';
import Container from '../components/styled/Container';
import { CopyHeading, BodyText } from '../components/styled/Typography';
import ProductExcerpts from '../components/products/ProductExcerpts';
import { viewports } from '../utils/variables';

const ProductsPage = ({ data }) => {
  const {
    wordpressPage: { acf },
  } = data;
  return (
    <Layout theme="secondary">
      <SecondaryPageWrapper>
        <TopBanner heading={acf.banner_heading} image={acf.banner_image} />
        <Container theme="secondary" style={containerCSS}>
          <div css={contentCSS}>
            <CopyHeading theme="large">{acf.products_heading}</CopyHeading>
            <BodyText theme="large">{acf.products_copy}</BodyText>
          </div>
        </Container>
        <ProductExcerpts products={acf.products} />
      </SecondaryPageWrapper>
    </Layout>
  );
};

export default ProductsPage;

const containerCSS = css`
  padding: 4.375rem 0 3.125rem;
  text-align: left;
`;

const contentCSS = css`
  margin: 0 auto;

  @media ${viewports.medium} {
    width: 80%;
  }
`;

export const query = graphql`
  query productsQuery($page_id: Int = 69) {
    wordpressPage(wordpress_id: { eq: $page_id }) {
      title
      acf {
        banner_heading
        banner_image {
          localFile {
            childImageSharp {
              fluid(maxWidth: 1920) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
        products_heading
        products_copy
        products {
          name
          description
          cta
          cta_url
          features {
            feature
          }
        }
      }
    }
  }
`;
